body {
  margin: 0;
  padding: 0;
  height: 100vh;
  font-family: "axiforma", sans-serif;
  background-color: #09090b;
  /* background-color: #3ebba0; */
  scroll-behavior: smooth;
  opacity: 0.8;
  background-image: radial-gradient(#202020 0.75px, #09090b 0.75px);
  background-size: 15px 15px;
  /* overflow: hidden; */
}
::-webkit-scrollbar {
  display: none;
  width: 12px;
  height: 12px;
}

::-webkit-scrollbar-track {
  background: #20232a;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #5b636e;
}

::-webkit-scrollbar-thumb:hover {
  background: rgb(77, 77, 77);
}
::-webkit-calendar-picker-indicator {
  filter: invert(100%);
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 1000px #09090b inset !important;
  -webkit-text-fill-color: rgb(250, 250, 250) !important;
  -webkit-font: "axiforma", sans-serif !important;
}

